/**
 *  2D Vector
 */
export class Vector {
    private _u: number
    private _v: number

    constructor(u: number, v: number) {
        this._u = u
        this._v = v
    }

    public get u(): number { return this._u }
    public get v(): number { return this._v }

    /**
     * Magnitude
     * @returns {Number}
     */
    public magnitude(): number {
        return Math.sqrt(this.u * this.u + this.v * this.v)
    }

    /**
     * Angle in degrees (0 to 360) --> Towards
     * N is 0 and E is 90
     * @returns {Number}
     */
    public directionTo(): number {
        const verticalAngle = Math.atan2(this.u, this.v)
        let inDegrees = verticalAngle * (180.0 / Math.PI)
        if (inDegrees < 0) {
            inDegrees = inDegrees + 360.0
        }
        return inDegrees
    }

    /**
     * Angle in degrees (0 to 360) From x-->
     * N is 0 and E is 90
     * @returns {Number}
     */
    public directionFrom(): number {
        const a = this.directionTo()
        const opposite = (a + 180.0) % 360.0
        return opposite
    }

    /*
        Degrees --> text
        new Dictionary<int, string>
        {
            //{0, 23, 45, 68, 90, 113, 135, 158, 180, 203, 225, 248, 270, 293, 315, 338, 360}
            {0, 'N'},
            {23, 'NNE'},
            {45, 'NE'},
            {68, 'ENE'},
            {90, 'E'},
            {113, 'ESE'},
            {135, 'SE'},
            {158, 'SSE'},
            {180, 'S'},
            {203, 'SSW'},
            {225, 'SW'},
            {248, 'WSW'},
            {270, 'W'},
            {293, 'WNW'},
            {315, 'NW'},
            {338, 'NNW'},
            {360, 'N'}
        }
    */
}


